.page-header {
  position: relative;

  .container();
  margin: 0 auto;

  background-color: @bg-white;

  z-index: 100;
}

.page-header__link {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  margin: auto;

  width: 185px;
  height: 96px;

  z-index: 300;
}

.page-header__image {
  position: relative;

  height: 158px;

  background-image: url("../img/back.jpg");
  background-repeat: no-repeat;
  background-position: center;

  background-color: @bg-brown;

  z-index: 200;

  @media (min-width: @tablet-width) {
    height: 290px;
  }

  @media (min-width: @desktop-width) {
    height: 300px;
  }

  &--main {
    height: 389px;

    @media (min-width: @tablet-width) {
      height: 518px;
    }
  }

  &::after {
    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    height: 26px;

    clip-path: polygon(0% 100%, 0% 0%, 50% 100%, 100% 0%, 100% 100%);

    background-color: @bg-white;

    content: "";

    @media (min-width: @tablet-width) {
      height: 37px;
    }

    @media (min-width: @desktop-width) {
      height: 57px;
    }
  }
}

.page-header__image img {
  position: absolute;

  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  margin: auto;

  @media (min-width: @tablet-width) {
    width: 460px;
    height: auto;
  }
}
